

.preloader {
  position: fixed;
  width: 100%;
  height: 100vh;
  background: rgba(255, 255, 255, 0.2);
  user-select: none;

  left: 0;
  right: 0;
  top: 0;
  bottom: 0;

  .preloaderImg {
    position: fixed;
    width: 15%;
    margin: auto auto;

    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
  }
}